<template>
    <el-container class="min-height">
        <el-header class="p-n">
            <div class="pull-left">
                <a @click="changeNav(!navStatus)">
                <i class="fa fa-outdent"></i>
                </a>
            </div>
            <div class="logo"></div>
            <nav-header class="pull-right"></nav-header>
        </el-header>
        <el-container>
            <nav-left style="background-color:#414141"></nav-left>
            <el-main>
                <router-view/>
            </el-main>
        </el-container>
    </el-container>
</template>

<script>
import navLeft from './nav'
import navHeader from './navHeader'
import {LOGIN, CHANGENAV, ROLETYPE} from '../vuex/mutations_type'
import {mapMutations, mapGetters} from 'vuex'
export default {
    created () {
        this.$httpMethods.login.getCurrentLoginUser().then(res => {
            if(res && res.status === 200) {
                this[LOGIN](res.data.data)
                this[ROLETYPE](res.data.data.roleList[0].id)
            }
        })
    },
    computed: {
        ...mapGetters(['navStatus'])
    },
    methods: {
        ...mapMutations([LOGIN, CHANGENAV, ROLETYPE])
    },
    components: {
        navLeft,
        navHeader
    }
}
</script>

<style>
    .min-height {
        min-height: 100%;
    }
    .el-header, .el-footer {
        background-color: #1b1b1b;
        color: #333;
        text-align: center;
        line-height: 60px;
    }

    .el-aside {
        background-color: #545C64;
        color: #333;
        overflow: hidden;
    }

    .el-main {
        background-color: #E9EEF3;
        color: #333;
        /*text-align: center;*/
        /*line-height: 160px;*/
        overflow: hidden;
        padding: 0;
    }

    body > .el-container {
        margin-bottom: 40px;
    }

    .logo {
        width: 201px;
        float: left;
        height: 1px;
    }
    .el-input__inner {
        height: 30px;
        font-size: 12px;
        border-radius: 1px;
        border: solid 1px #cad8df;
    }
    .el-input__icon {
        line-height: 30px;
    }
    .join-dialog .el-dialog {
        width: 500px;
    }
    .join-dialog .join-panel {
        width: 360px;
        margin: auto;
    }
    .join-dialog .join-tip {
        padding: 14px 29px 14px 11px;
        background-color: rgba(236, 103, 11, 0.1);
        color: #f08d48;
    }
    .join-dialog .join-tip > div {
        width: 30px;
        height: 50px;
    }
    .join-dialog .join-body {
        margin-top: 16px;
    }
    .join-dialog .join-search {
        position: relative;
    }
    .join-dialog .join-search input {
        width: 360px;
        height: 30px;
        background-color: #ffffff;
        border-radius: 1px;
        border: solid 1px #cad8df;
        padding: 10px 25px 10px 10px;
        box-sizing: border-box;
        outline: none;
    }
    .join-dialog .join-search span {
        position: absolute;
        font-size: 16px;
        color: #b0bec5;
        right: 7px;
        top: 2px;
    }
    .join-dialog .join-search-body {
        width: 360px;
        height: 200px;
        background-color: #ffffff;
        border-radius: 1px;
        border: solid 1px #cad8df;
        box-sizing: border-box;
        margin-top: 3px;
        overflow-y: auto;
    }
    .join-dialog .join-search-body > div {
        height: 40px;
        padding: 6px 9px;
        box-sizing: border-box;
        font-size: 12px;
        line-height: normal;
        cursor: pointer;
    }
    .join-dialog .join-search-body > div:hover,
    .join-dialog .join-search-body > div.is-active {
        background: #f5f7f8;
    }
    .join-dialog .join-search-body > div > span:last-child {
        color: #bcc5d9;
    }
</style>



