<template>
    <div>
        <el-menu
        class="el-menu-demo"
        mode="horizontal"
        @select="handleSelect"
        background-color="#1b1b1b">
            <div class="header-message">
                200
            </div>
            <div class="header-message">
                帮助
            </div>
            <div class="user-message" style="float: left">
                <div class="user-image">
                    <img src="../assets/img/touxiang.jpg" class="user-image-header">
                </div>
                <div class="user-info">
                    <span>幻星辰</span>
                    <br>
                    <span>教师</span>
                </div>
            </div>
            <el-submenu index="1">
                <el-menu-item index="2" @click="logout">
                    <span v-text="$t('menu.logout')"></span>
                </el-menu-item>
            </el-submenu>
        </el-menu>
    </div>
</template>
<script>
import { LOGOUT } from '../vuex/mutations_type'
import { mapGetters } from 'vuex'
import { removeToken } from '../assets/js/toekn'

export default {
  computed: {
    ...mapGetters(['loginUsername'])
  },
  methods: {
    handleSelect (key, keyPath) {
      console.log(key, keyPath)
    },
    logout () {
      this.$store.commit(LOGOUT)
      removeToken()
      this.$router.push({name: 'login'})
    }
  }
}
</script>
<style scoped>
  .el-menu-demo {
    display: flex;
    align-items: center;
  }
  .header-message {
    color: #bcc5d9;
    border-right: 1px solid #bcc5d9;
    height: 30px;
    margin-right: 15px;
    padding-right: 15px;
    font-size: 14px;
    float: left;
    line-height: 30px;
  }
  .header-message:focus {
    outline: none;
  }
  .user-image {
    padding: 0;
    height: 40px;
    width: 40px;
    border-radius: 20px;
    overflow: hidden;
    display: block;
  }
  .user-image-header {
    height: 40px;
    width: 40px;
    vertical-align: top;
  }
  .user-message {
    display: flex;
    align-items: center;
  }
  .user-message:focus {
    outline: none;
  }
  .user-info {
    margin-left: 15px;
    display: block;
    line-height: normal;
    text-align: left;
  }
  .user-info > span {
    font-size: 14px;
    display: inline;
    color: white;
  }
  .user-info > span:first-child {
    color: #0c92fe;
  }
</style>
<i18n>
    {
        "zh": {
            "menu": {
                "logout": "注销"
            }
        }
    }
</i18n>



