<template>
    <el-row>
        <el-col :span="24">
            <el-menu :default-active="$route.path" class="el-menu-vertical-demo border-g"
                     background-color="#414141" text-color="#bcc5d9" active-text-color="#0c92fe"
                     @open="handleOpen" @close="handleClose" :collapse="navStatus">
                <el-menu-item index="/home/user" @click="link('/home/user')">
                    <i class="el-icon-menu"></i>
                    <span slot="title">{{$t('nav.user')}}</span>
                </el-menu-item>
                <el-menu-item index="/home/homework" @click="link('/home/homework')">
                    <i class="el-icon-menu"></i>
                    <span slot="title">{{$t('nav.homework')}}</span>
                </el-menu-item>
                <el-menu-item index="/home/group" @click="link('/home/group')">
                    <i class="el-icon-menu"></i>
                    <span slot="title">{{$t('nav.group')}}</span>
                </el-menu-item>
                <el-menu-item index="/home/student" @click="link('/home/student')">
                    <i class="el-icon-menu"></i>
                    <span slot="title">{{$t('nav.student')}}</span>
                </el-menu-item>
                <el-menu-item index="/home/teacher" @click="link('/home/teacher')">
                    <i class="el-icon-menu"></i>
                    <span slot="title">{{$t('nav.teacher')}}</span>
                </el-menu-item>
                <el-menu-item index="/home/class" @click="link('/home/class')">
                    <i class="el-icon-menu"></i>
                    <span slot="title">{{$t('nav.class')}}</span>
                </el-menu-item>
                <el-menu-item index="/home/collage" @click="link('/home/collage')">
                    <i class="el-icon-menu"></i>
                    <span slot="title">{{$t('nav.collage')}}</span>
                </el-menu-item>
                <el-menu-item index="/home/myClass" @click="link('/home/myClass')">
                    <i class="el-icon-menu"></i>
                    <span slot="title">{{$t('nav.myClass')}}</span>
                </el-menu-item>
                <el-menu-item index="/home/netdisk" @click="link('/home/netdisk')">
                    <i class="el-icon-menu"></i>
                    <span slot="title">{{$t('nav.netdisk')}}</span>
                </el-menu-item>
                <el-menu-item index="/home/parameter" @click="link('/home/parameter')">
                    <i class="el-icon-menu"></i>
                    <span slot="title">{{$t('nav.parameter')}}</span>
                </el-menu-item>
                <el-menu-item index="/home/apply" @click="link('/home/apply')">
                    <i class="el-icon-menu"></i>
                    <span slot="title">{{$t('nav.apply')}}</span>
                </el-menu-item>
                <!-- <el-menu-item index="/home/system" @click="link('/home/system')">
                    <i class="el-icon-menu"></i>
                    <span slot="title">{{$t('nav.system')}}</span>
                </el-menu-item> -->
                <!-- <el-menu-item index="/home/operationLog" @click="link('/home/operationLog')">
                    <i class="el-icon-menu"></i>
                    <span slot="title">{{$t('nav.operationLog')}}</span>
                </el-menu-item> -->
            </el-menu>
        </el-col>
    </el-row>
</template>

<script>
    import {mapGetters} from 'vuex'

    export default {
        computed: {
            ...mapGetters(['navStatus'])
        },
        methods: {
            handleOpen(key, keyPath) {
                console.info(key, keyPath)
            },
            handleClose(key, keyPath) {
                console.info(key, keyPath)
            },
            link(url) {
                if (url) {
                    this.$router.push(url)
                }
            }
        }
    }
</script>

<style>
    .el-menu-vertical-demo:not(.el-menu--collapse) {
        width: 280px;
        /*height: 1080px;
        min-height: 1080px;*/
        min-height: 100%;
    }

    .border-g {
        border-color: #545C64;
    }

    .el-menu-vertical-demo .el-menu-item-head {
        height: 30px;
        line-height: 30px;
    }

    .el-menu-vertical-demo .el-menu-item.is-active {
        padding-left: 0 !important;
    }

    .el-menu-vertical-demo .el-menu-item:hover {
        background: rgba(237, 237, 244, 0.1) !important;
    }

    .el-menu-vertical-demo .is-active {
        background: rgba(237, 237, 244, 0.1) !important;
    }

    .el-menu-vertical-demo .is-active::before {
        content: "";
        display: block;
        background-color: #0c92fe;
        height: 100%;
        width: 6px;
        float: left;
        margin-right: 15px;
    }

    hr {
        border-color: rgba(0, 0, 0, 0.4);
        margin: 5px 20px;
    }
</style>
<i18n>
    {
        "zh": {
            "nav": {
                "user": "用户",
                "group": "课程组管理",
                "homework": "作业管理",
                "student": "学生管理",
                "teacher": "教师管理",
                "class": "班级管理",
                "collage": "学院管理",
                "parameter": "参数配置",
                "system": "系统管理",
                "operationLog": "操作日志",
                "netdisk": "个人网盘",
                "myClass": "我的班级",
                "apply": "审批"
            }
        }
    }
</i18n>




